import {NgModule} from '@angular/core';
import {Routes, RouterModule} from '@angular/router';
import {BusinessComponent} from './business/business.component';
import {ItoptimizationComponent} from './itoptimization/itoptimization.component';
import {ItmanagementComponent} from './itmanagement/itmanagement.component';
import {ItoperationsComponent} from './itoperations/itoperations.component';
import {ItprocessComponent} from './itprocess/itprocess.component';
import {AdvisoryserviceComponent} from './advisoryservice/advisoryservice.component';


const routes: Routes = [
  {
    path: '',
    component: BusinessComponent
  },
  {
    path: 'it-optimization',
    component: ItoptimizationComponent
  },
  {
    path: 'it-management',
    component: ItmanagementComponent
  },
  {
    path: 'it-operations',
    component: ItoperationsComponent
  },
  {
    path: 'it-process',
    component: ItprocessComponent
  },
  {
    path: 'advisory',
    component: AdvisoryserviceComponent
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class BusinessChildRoutingModule {
}
